.dropzone > div{
  width: 100%!important;
  background: rgba(120,120,120,0.2);
}

.dropzone > div::after{
  content: 'Drop files here';
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}



